/* Scss Document */
.box_bg_gray {
    background: #eee;
    padding: 15px;
}

.title_bx {
    font: 400 px_to_rem(18px) $font-title;
    @extend %font-fea;
    line-height: $lh;
    padding: 0 0 10px;
    //border-bottom: 1px solid #e7e7e7;
    margin-bottom: 7px;
    text-align: left;

    &.no_border {
        border: none;
        padding: 0;
    }
}

.item_row_bx {
    padding: 5px 0;
    text-align: left;

    .label_check {
        width: 100%;
        float: left;
        position: relative;
        padding-left: 20px;
        text-align: left;

        .radio_check {
            position: absolute;
            top: 12px;
            left: 14px;
        }
    }
    span {
        font: 400 px_to_rem(17px) arial;
        line-height: $lh;
    }

    &.item_row_border {
        border-bottom: 1px solid #fff;

        &.last_rating {
            border: none;
        }
    }

}

.box_button {
    text-align: left;

    &.box_button_center {
        text-align: center;
    }
}

.btn_vote_rate {
    &.btn_ketqua {
        color: #999 !important;
        font: 400 px_to_rem(16px) arial;
        padding: 8px 5px;
        background: none !important;
        text-decoration: none;
    }
}



