.vote_rating_box {
    padding: 15px 0;
    width: 100%;
    float: left;
    text-align: left;
    background: #F7F7F7;
    // margin: 0 -15px 15px -15px;
    // width: calc(100% + 30px) !important;
    border-top: 1px solid #e5e5e5;
    border-bottom: 1px solid #e5e5e5;

    .title-box-category {
        margin-bottom: 10px;
        position: relative;
        padding: 0 15px;

        .inner-title {
            color: $vne_color;
            font-family: $font-title;
            @extend %font-fea;
            font-size: 16px;
            line-height: 1.25;
            font-weight: normal;
            &:before {
                display: none;
            }
        }
    }

    .title_bx {
        font-size: 20px;
        font-weight: 700;
        font-size: 18px;
        margin-bottom: 0;
    }

    .label_check {
        font-size: 18px;
        line-height: 21px;
        background: #E5E5E5;
        border-radius: 3px;
        padding: 8px 12px 8px 38px;

        input.radio_check[type="radio"] {
            width: 16px;
            height: 16px;
            top: 12px;
            left: 12px;

            &:before {
                content: '';
                width: 16px;
                height: 16px;
                background: #fff;
                border: 1px solid #BDBDBD;
                border-radius: 50%;
                position: absolute;
                left: -1px;
                top: -1px;
            }

            &:checked {
                &:before {
                    border-color: #9f224e;
                }

                &:after {
                    content: '';
                    width: 8px;
                    height: 8px;
                    background: #9f224e;
                    border-radius: 50%;
                    position: absolute;
                    left: 4px;
                    top: 4px;
                }
            }
        }

        input.checkbox_check[type="checkbox"] {
            width: 16px;
            height: 16px;
            position: absolute;
            left: 12px;
            top: 12px;
            border: none;
            background: #fff;

            &:before {
                content: '';
                width: 16px;
                height: 16px;
                background: #fff;
                border: 1px solid #BDBDBD;
                position: absolute;
                left: 0;
                top: 0;
            }

            &:checked {
                &:before {
                    border-color: #9f224e;

                }

                &:after {
                    content: '';
                    width: 4px;
                    height: 8px;
                    border-bottom: 2px solid #9f224e;
                    border-right: 2px solid #9f224e;
                    display: block;
                    position: absolute;
                    left: 6px;
                    top: 3px;
                    transform: rotate(45deg);
                }
            }
        }
    }

    .btn_view_cancel {
        background: none;
        text-decoration: underline;
        color: #076DB6 !important;
        padding-left: 20px;
    }
    .box_button{
        margin: 10px 0 0 0 !important;
    }
}

.content-box-category {
    &.col50 {
        .item_row_bx {
            width: calc(50% - 10px);

            &:nth-child(even) {
                float: left;
            }

            &:nth-child(odd) {
                float: right;
            }
        }
    }
}
.btn_vne{
    &.btn_disable {
        background: #e2e0e0 !important;
        color: #BDBDBD !important;
    }
}
