.box_rating {
    .title-box-vote{font-size: 16px; margin-bottom: 10px; color: #9F224E; line-height: 160%;float: left; font-family: $font-title;}
    .box_bg_gray {
        background: #f7f7f7;
    }

    .form_element {
        margin-top: 0;
        padding-top: 0;
        border: none;
    }

    .item_row_bx {
        padding: 0;
        background: #eee;

        span {
            padding: 13px 0 13px 15px;
            display: inline-block;
        }

        .number_point {
            background: $vne_color;
            color: #fff;
            padding: 13px 0;
            width: 55px;
            text-align: center;
        }
    }
    .count-vote{position: static; float: right; margin-top: 5px;}
    .title_bx{margin-bottom: 5px;}
}

.item-point{width: 100%; max-width: 155px;
    .row-number{align-items: center;}
    .star-point{margin-right: 10px;
      .ic{width: 40px;height: 40px; fill: #F2C94C;}
    }
    .number-star{font-size: 18px; color: #757575;
      .point-bold{font-size: 24px; font-weight: 700;color: #222;}
      .point-small{line-height: 34px;}
      span{display: inline-block; vertical-align: bottom;}
    }
    .txt-point{font-size: 16px; margin-top: 10px;}
    &.point-your{margin-right: 0; cursor: pointer; margin-left: auto;
      .star-point{
        .ic{fill: #E5E5E5;}
      }
      .number-star{
        .point-bold{color: #757575;}
      }
      &.your-rate{
        .star-point{
          .ic{fill: #0887E2;}
        }
        .number-star{
          .point-bold{color: #222;}
        }
      }
    }
  }
  .title-rate-pop{
    font: 700 18px/160% $font-title; text-align: left; margin-bottom: 15px !important;
    @extend %font-fea;
  }
  
  .block_one_start{width: 40px; height: 40px; font-size: 0; margin: 0 20px 0 0;
    .star{display: inline-block; width: 20px; overflow: hidden;
      .ic{fill: #E5E5E5; width: 40px;height: 40px;}
    &.star-right{
      .ic{margin-left: -20px;}
    }
    }
    .star.hover,.star:hover{
        .ic{fill: #0887E2;}
    }

    &:last-child {
        margin-right: 0;
    }
  
  }
  @media screen and (max-width: 374px){
    .block_one_start{margin: 0 10px 0 0;}
  }

/*lightbox*/
.text_box_lb {
    border-bottom: 1px solid #E5E5E5;
    font-family: $font-title;
    @extend %font-fea;
    font-size: 18px;
}
.no_title .mfp-close {
    display: inline-block !important;
}
.voteresult-popup {
    .text_box_lb {
        border-bottom: 1px solid #E5E5E5;
        font-family: $font-title;
        @extend %font-fea;
        font-size: 18px;
    }

    .vote_rating_box {
        background: #fff;

        .item-kq-vote:last-of-type{
            margin-bottom: 20px;
        }
    }

    .title_bx {
        border-bottom: none;
        padding-bottom: 0;
        margin-bottom: 15px;
    }

    &.no_title .mfp-close {
        display: block !important;
    }

    .content-box-category {
        margin-bottom: 0;
        padding: 0 15px;
    }
}

/*End lightbox*/
.section_common {
    #boxthamdoykien.vote_rating_box {
        width: calc(100% + 30px) !important;
        margin-left: -15px;
    }
}

@media (max-width: 320px){
    .mfp-with-zoom.mfp-ready .white-popup{
        width: calc(100% + 20px);
        margin-left: -10px;
    }
    .voteresult-popup .content-box-category{
        padding: 0 10px;
    }
}